import "..//styles/bigheader.css";
import posterimg from "..//media/images/img_1.jpg";
import logo from "../media/images/logo.png"
import searchicon from "..//media/images/search3.png"
import react, { useEffect, useState } from "react"
import up from "..//media/images/up-arrow.png"


import misa_logo from "..//media/images/sponsors/misa.jpg";
import map_logo from "..//media/images/sponsors/map.jpg";
import a2s_logo from "..//media/images/sponsors/a2s.png";
import hitradio_logo from "..//media/images/sponsors/hitradio.png";
import snrt_logo from "..//media/images/sponsors/snrt.png";
import xhub_logo from "..//media/images/sponsors/xhub.png";
import benani_logo from "..//media/images/sponsors/mehdi_benani.jpeg";



import $ from "jquery"
const Footer = () => {
    return (
        <div className="footer_area">
            <h4>
                Sponsored by  :
            </h4>
            <div className="footer_space">
                <div className="bottom_box">
                    <div className="sponsor_place">
                        <img src={hitradio_logo} className="sponsor_logo" />
                        <h4>
                            hit radio
                        </h4>
                    </div>
                    <div className="sponsor_place">
                        <img src={snrt_logo} className="sponsor_logo" />
                        <h4>
                            snrt
                        </h4>
                    </div>
                    <div className="sponsor_place">
                        <img src={benani_logo} className="sponsor_logo" />
                        <h4>
                            mehdi bennani
                        </h4>
                    </div>
                    <div className="sponsor_place">
                        <img src={xhub_logo} className="sponsor_logo" />
                        <h4>
                            X-hub
                        </h4>
                    </div>
                    <div className="sponsor_place">
                        <img src={misa_logo} className="sponsor_logo" />
                        <h4>
                            mesa-morroco
                        </h4>
                    </div>
                    <div className="sponsor_place">
                        <img src={a2s_logo} className="sponsor_logo" />
                        <h4>
                            A2S
                        </h4>
                    </div>
                    <div className="sponsor_place">
                        <img src={map_logo} className="sponsor_logo" />
                        <h4>
                            MAP
                        </h4>
                    </div>
                </div>
            </div>
        </div>
    );
}
const Searchbar = () => {
    return (
        <div className="searchbar">
            <div className="searchicon">
                <img className="" width="20px" height="20px" src={searchicon} />
            </div>
            <input placeholder="search" className="search" />
        </div>
    );
}
const handleup = (e) => {
    e.preventDefault()
    var element = document.documentElement;
    element.scrollTop = element.scrollHeight - element.clientHeight;
}
const BigheaderText = () => {
    return (
        <div className="bigheadertext">
            <h2 className="welcome">Welcome to Esport academy ! </h2>
            <h1 className="new_g">THΞ NΞW GΞNΞRΔTION OF ΞSPORT ΞVΞNT</h1>
            <h3 className="text">Esport refers to the practice of video games in the form of a  professionally organized competition for Gaming fans  , by professional players, and monetized as  a media event.
                Esport today officially recognized by sports bodies  as a sport in the same way as football. In  addition, the professionalization of the discipline brings  investment challenges in professional teams and  eSport structures.</h3>
            <div onClick={(e) => { handleup(e) }} className="explore_btn">
                <h4 >Explore our Programs ! </h4>
            </div>
        </div>
    );
}
const Bigheader = () => {
    useEffect(() => {
        $(function () {
            $(window).on('scroll', function () {
                if ($(window).scrollTop() > 150) {
                    $('#change_by_scrool').addClass('big_header_poster_top_header_options_after_scroll');
                    $('.explore_btn').addClass('displaynone');
                    $('.welcome').addClass('displaynone');
                    $('.text').addClass('displaynone');
                    $('#logo_text').css({ 'color': '#8a2be2' });
                } else {
                    $('#change_by_scrool').removeClass('big_header_poster_top_header_options_after_scroll');
                    $('.explore_btn').removeClass('displaynone');
                    $('.welcome').removeClass('displaynone');
                    $('.text').removeClass('displaynone');
                    $('#logo_text').css({ 'color': '#6f627c' });
                }
            });
        });
    });
    return (
        <div >
            <div className="big_header_poster" >
                <div className="big_header_poster_insider">
                    <div className="big_header_poster_insider_2" >
                        <header className="big_header_poster_top_header">
                            <div id="change_by_scrool" className="big_header_poster_top_header_options">
                                <div className="lan_btn">
                                    <select>
                                        <option value="francais">frencais</option>
                                        <option value="english">english</option>
                                    </select>
                                </div>
                                <div id="logo_text" className="logo_text_before_scroll">
                                    ΞSPORT ΔCΔDΞMY
                                </div>
                            </div>
                            <div className="big_header_poster_logo_outer">
                                <img src={logo} className="big_header_poster_logo" width="90px" height="90px" />
                            </div>
                            <div className="card">
                                <BigheaderText></BigheaderText>
                            </div>
                        </header>
                    </div>
                    <Footer></Footer>
                </div>
            </div>
            <div className="container">
                <div className="container_insider">
                </div>
            </div>
        </div>
    );
}
export default Bigheader;